import React from "react";
import SignUpHeader from "../../header/LoginHeader.js";

const Stage2 = props => {
  return (
    <div>
      <div style={{ display: "flex", justifyContent: "center" }}>
        <SignUpHeader classname={"comingFromRight"}></SignUpHeader>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          marginBottom: 100
        }}
      >
        <form onSubmit={props.onSubmit}>
          <div className="notification" style={{ background: "#ffffff" }}>
            <div
              className={`box ${
                props.isBack ? "comingFromLeftP5" : "comingFromRightP5"
              }`}
              style={{ marginLeft: 10 }}
            >
              <div className={"field comingFromRight"}>
                <p className={"control has-icons-left has-icons-right"}>
                  <input
                    className={`input`}
                    type="text"
                    name="username"
                    placeholder="Username"
                    value={props.username}
                    onChange={props.onChangeHandler}
                  ></input>
                  <span className="icon is-small is-left">
                    <i className="fas fa-user"></i>
                  </span>
                </p>
                {!props.isUsernameCorrect ? (
                  <p className="help is-danger comingFromLeftP5">
                    {"Enter Valid Username"}
                  </p>
                ) : null}
              </div>
            </div>

            <div
              className={`box ${
                props.isBack ? "comingFromLeftP5" : "comingFromRightP5"
              }`}
              style={{ marginLeft: 10 }}
            >
              <div
                className={`field ${
                  props.isBack ? "comingFromLeftP5" : "comingFromRightP5"
                }`}
              >
                <p className={"control has-icons-left has-icons-right"}>
                  <input
                    className={`input`}
                    type="tel"
                    name="phone"
                    placeholder="Phone"
                    value={props.phone}
                    onChange={props.onChangeHandler}
                  ></input>
                  <span className="icon is-small is-left">
                    <i className="fas fa-phone"></i>
                  </span>
                </p>
              </div>
              {props.NotValidPhone ? (
                <p className="help is-danger comingFromLeftP5">
                  {"Enter A Valid Phone"}
                </p>
              ) : null}
            </div>

            <div
              className={`box ${
                props.isBack ? "comingFromLeftP5" : "comingFromRightP5"
              }`}
              style={{ marginLeft: 10 }}
            >
              <div
                className={`field ${
                  props.isBack ? "comingFromLeftP5" : "comingFromRightP5"
                }`}
              >
                <div className="select">
                  <select
                    placeholder="Select College"
                    onChange={e => {
                      props.setState({
                        organization: e.target.value,
                        isCollegeSelected: true
                      });
                    }}
                  >
                    {!props.isSelected ? (
                      <option value={""}>Select College</option>
                    ) : (
                      ""
                    )}
                    <option value={"SDIET"}>SDIET</option>
                  </select>
                </div>
              </div>
              {props.noOrg ? (
                <p className="help is-danger comingFromLeftP5">
                  {"Please Select The College"}
                </p>
              ) : null}
            </div>

            <div
              className={"notification"}
              style={{
                background: "#ffffff",
                marginLeft: 10,
                display: "flex",
                justifyContent: "center",
                flexDirection: "column"
              }}
            >
              <button
                className={`button is-primary comingFromRightP5 ${
                  props.isLoading ? "is-loading" : null
                }`}
                style={{ marginLeft: 10 }}
                type="submit"
              >
                <i className="fas fa-arrow-right arrowRight"></i>
              </button>

              <button
                type="button"
                className="button is-info comingFromRight"
                style={{ marginTop: 10, marginLeft: 10 }}
                onClick={event => props.onBack()}
              >
                <i className="fas fa-arrow-left arrowLeft"></i>
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Stage2;
